
.font-size-14{font-size:14px
}
.vertical-middle{position:absolute;top:50%;transform:translateY(-50%)
}
.tran-center{position:absolute;left:50%;transform:translateX(-50%)
}
.flex-center{display:-webkit-flex;display:flex;-webkit-align-items:center;-ms-flex-align:center;align-items:center;justify-content:center
}
.appearance-none{border:0;background-color:white;-webkit-appearance:none;-moz-appearance:none;appearance:none
}
.clearFix:after{display:block;clear:both;content:''
}
.img-responsive,.GoddessHome-view .box-list .item-skill .coverImg-wrapper .wrapper{overflow:hidden;height:0;padding-bottom:100%;background-color:#fff
}
.font-size-10,.GoddessHome-view .mask .constellation,.GoddessHome-view .mask .sex{font-size:12px;transform:scale(0.8, 0.8)
}
.mask{width:100%;height:100%;background-color:rgba(0,0,0,0.5)
}
.sprite{background-image:url(http://ogf0xbf8w.bkt.clouddn.com/spritesheet.png);background-repeat:no-repeat;display:inline-block
}
.sprite_selected{width:14px;height:14px;background-position:-2px -2px
}
.sprite.people{width:14px;height:16px;background-position:-20px -2px
}
.GoddessHome-view .skillListTitle{background-color:white
}
.GoddessHome-view .box-list{margin-top:10px
}
.GoddessHome-view .box-list .item-skill{max-height:150px;background-color:white;border-bottom:1px solid #EFEFF4;padding:4%;overflow:hidden;display:flex
}
.GoddessHome-view .box-list .item-skill .coverImg-wrapper{width:35%
}
.GoddessHome-view .box-list .item-skill .coverImg-wrapper .wrapper img{width:100%;border-radius:5px
}
.GoddessHome-view .box-list .item-skill .skillInfo{width:calc(65% - 8px);width:-webkit-calc(65% - 8px);width:-moz-calc(65% - 8px);font-size:14px;color:#6F6F6F;margin-left:8px;font-weight:200
}
.GoddessHome-view .box-list .item-skill .skillInfo .icon-liulanrenshu{font-size:14px;line-height:20px
}
.GoddessHome-view .box-list .item-skill .skillInfo .name{float:left;font-size:15px;font-weight:bold
}
.GoddessHome-view .box-list .item-skill .skillInfo .name *{vertical-align:middle
}
.GoddessHome-view .box-list .item-skill .skillInfo .skillDescription{color:#6F6F6F;line-height:1.2;max-height:203px;margin:8px 0;overflow:hidden
}
.GoddessHome-view .box-list .item-skill .skillInfo .skillFee{color:#FF5572
}
.GoddessHome-view .flex-box{display:flex;justify-content:space-between;color:black
}
.GoddessHome-view .vux-header{padding:0
}
.GoddessHome-view .vux-header .vux-header-title{font-size:15px;padding:0 20px;color:#FF5572 !important;border-bottom:1px solid #FF5572
}
.GoddessHome-view .GoddessInfo-box{height:276px;background-size:cover;background-repeat:no-repeat
}
.GoddessHome-view .v{width:18px;height:18px;border-radius:18px;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box;border:1px solid white;font-size:14px;vertical-align:bottom;font-weight:200;color:white;background-color:#FF5572;margin-top:-23px;right:5px;position:absolute
}
.GoddessHome-view .mask{text-align:center;color:white;padding-top:30px;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box
}
.GoddessHome-view .mask .headPortrait-wrapper{width:70px;margin:0 auto;position:relative
}
.GoddessHome-view .mask .headPortrait-wrapper .headPortrait{width:70px;height:70px;border-radius:70px;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box;border:1px solid white
}
.GoddessHome-view .mask h1{font-size:15px;line-height:1.2
}
.GoddessHome-view .mask h2{font-size:14px;font-weight:200;color:#BDC1C6
}
.GoddessHome-view .mask .sex{background-color:#FFA2BC
}
.GoddessHome-view .mask .constellation{background-color:#A7E5D0;margin-left:10px
}
.GoddessHome-view .mask .signature{font-size:14px;color:#BDC1C6;line-height:2
}
.GoddessHome-view .mask .constellation,.GoddessHome-view .mask .sex{display:inline-block;line-height:20px;width:63px;border-radius:20px
}
.GoddessHome-view .mask .icon-icStar{font-size:14px;margin-top:14px;line-height:1;vertical-align:bottom
}
.GoddessHome-view .mask .icon-icStar:before{margin-right:8px
}
.GoddessHome-view .icon-BackArrow{color:white;position:fixed;left:15px;font-size:18px;line-height:40px
}
.GoddessHome-view .header{height:40px;background-color:white
}
.GoddessHome-view .header .title{font-size:15px;width:100px;text-align:center;line-height:38px;margin:0 auto;color:#FF5572 !important;border-bottom:2px solid #FF5572
}
.footer{color:#FF5572;height:50px;text-align:center;line-height:50px;position:absolute;bottom:0;width:100vw;background-color:white;border-top:1px solid #D0D5D7;font-size:18px
}



