html,body{height:100%;line-height:1
}
html{font-size:62.5%
}
body{background-color:#EFEFF4
}
.forward-enter,.forward-leave-active{transform:translate3d(-100%, 0, 0)
}
.back-enter,.back-leave-active{transform:translate3d(100%, 0, 0)
}
.vux-pop-out-enter-active,.vux-pop-out-leave-active{width:100%;animation-duration:0.5s;animation-fill-mode:both;backface-visibility:hidden
}
.vux-pop-out-enter,.vux-pop-out-leave,.vux-pop-in-enter,.vux-pop-in-leave{will-change:transform;height:100%;position:absolute;left:0
}
.vux-pop-out-enter{animation-name:popInLeft
}
.vux-pop-out-leave{animation-name:popOutRight
}
.vux-pop-in-enter{perspective:1000;animation-name:popInRight
}
.vux-pop-in-leave{animation-name:popOutLeft
}
@keyframes popInLeft{
from{transform:translate3d(-100%, 0, 0)
}
to{transform:translate3d(0, 0, 0)
}
}
@keyframes popOutLeft{
from{transform:translate3d(0, 0, 0)
}
to{transform:translate3d(-100%, 0, 0)
}
}
@keyframes popInRight{
from{transform:translate3d(100%, 0, 0)
}
to{transform:translate3d(0, 0, 0)
}
}
@keyframes popOutRight{
from{transform:translate3d(0, 0, 0)
}
to{transform:translate3d(100%, 0, 0)
}
}



